html,body,#root,.App{
    height:100%;
}
*{
    box-sizing: border-box;
}
.App{
    /* position: relative; */
    /* overflow-y: scroll;
    scroll-behavior: smooth; */
    /* background-color: rgb(228, 198, 101); */
}
#container {
    display: flex;
    flex-direction: column;
    position: absolute;
    overflow: auto;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .page {
    height:100vh;
    min-height:100%;
    display: flex;
    position: relative;
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
    /* background-color: gray; */
    color: #000;
  }
  .projects{
      color:white;
  }
  .carousel-caption {
    position: relative;
    left: auto;
    right: auto;
}
